    .faq-page-section {
        @include padding(100 0 50);

        .heading-block {
            @include border-bottom(1, solid, lighten($theme, 30%));
            @include padding-bottom(10);

            h3 {
                @include fontsize(30);
                font-weight: 400;
                color: $theme;
            }
        }

        .search-field {
            @include margin-top(70);
            @include pos(rel, null);
            .search-field-control {
                width: 100%;
                height: 50px;
                @include border-round(4);
                @include padding(0 15 0 40);
                @include fontsize(20);
                @include border(1, solid, #b7b7c0);
                @include box-shadow(inset 0 2px 7px rgba(0, 0, 0, .11));
            }

            .ico {
                @include pos(ab, 50% null null 10px);
                @include translatepos(0, -50%);
                cursor: pointer;
            }
        }

        .page-content {
            @include margin-top(70);
            .f-nav-list {
                @include padding(30 0);
                @include margin-top(30);
                @include border-left(1, solid, #eaeaea);
                &>ul {
                    &>li {
                        @include fontsize(18);
                        font-weight: 300;
                        @include padding-left(30);
                        @include padding-bottom(25);
                        color: $color-text-02;
                        text-transform: uppercase;

                        a {
                            @include pos(rel, null);
                            color: $color-text-02;
                            &:before {
                                width: 6px;
                                height: 0px;
                                @include pos(ab, 0 null 0 -33px);
                                background-color: $theme;
                                content: "";
                                -webkit-transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
                                transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
                            }

                            &.active {
                                &:before {
                                    height: 18px;
                                }
                            }

                            &:hover {
                                &:before {
                                    height: 18px;
                                }
                            }
                        }
                    }
                }
            }

            .content-data {
                h3.title {
                    @include fontsize(28);
                    color: $theme;
                    font-weight: 200;
                }

                .__inner-content {
                    @include margin-top(20);
                    color: $color-text-02;

                    h4 {
                        @include fontsize(20);
                        font-weight: 300;
                        @include padding-bottom(20);
                    }

                    ul,
                    ol {
                        li {
                            @include padding-bottom(10);
                        }
                    }

                    p {
                        line-height: 25px;
                    }
                }

                .contact-form {

                    h3 {
                        @include fontsize(18);
                        font-weight: 500;
                        color: $color-text-02;
                    }

                    .field-group {
                        max-width: 550px;
                        margin: 35px 0 0 0;

                        .c-field {
                            label {
                                @include fontsize(18);
                                text-transform: uppercase;
                            }

                            .c-field-control {
                                border: 0;
                                color: $color-text-02;
                            }
                        }
                    }
                }
            }
        }
    }