.step-form-block {
   @include margin-top(-330);

   .title {
      @include fontsize(35);

      text-align: center;
      font-weight: 300;
      color: $white;
   }

   .step-form-container {
      background-color: $white;

      @include margin-top(50);

      .step-form-heading {
         background-color: $theme;
         text-align: center;

         @include padding(50 0);

         color: $white;

         h3.title {
            @include fontsize(28);

            font-weight: 400;
         }

         &.h-sm {
            @include padding(30 0 25);
         }
      }

      .step-form-data {
         @include padding(25 0);
      }

      .step-form-field-box {
         max-width: 500px;
         margin: 0 auto;
      }

      .step-form-nav {
         max-width: 430px;
         margin: 0 auto;

         ul {
            display: table;
            width: 100%;

            li {
               float: left;
               width: 50%;

               @include pos(rel, null);

               z-index: 0;

               .step-nav-box {
                  width: 100px;
                  height: 100px;

                  @include circle;

                  background-color: $white;
                  text-align: center;

                  @include border(2, dashed, #d7d7d7);

                  display: table;
                  -webkit-transition: all 600ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
                  transition: all 600ms cubic-bezier(0.6, -0.28, 0.735, 0.045);

                  .step-nav-txt {
                     display: table-cell;
                     vertical-align: middle;
                  }

                  .label-step {
                     display: $b;

                     @include fontsize(22);

                     font-weight: 500;
                     line-height: 1.2;
                     color: lighten($color-text-01, 30%);
                  }

                  .step-no {
                     display: $b;

                     @include fontsize(28);

                     font-weight: 500;
                     line-height: 1.2;
                     color: lighten($color-text-01, 30%);
                  }
               }

               .step-one {
                  &:after {
                     @include pos(ab, 50% 0 0 0);

                     content: "";
                     height: 2px;

                     @include border-top(2, dashed, #d7d7d7);

                     width: 100%;
                     z-index: -1;
                  }
               }

               .step-two {
                  float: right;

                  &:after {
                     @include pos(ab, 50% 0 0 null);

                     content: "";
                     height: 2px;

                     @include border-top(2, dashed, #d7d7d7);

                     width: 100%;
                     z-index: -1;
                  }
               }

               .active {
                  @include border(2, solid, $theme);

                  background-color: $theme;

                  .label-step {
                     color: $white;
                  }

                  .step-no {
                     color: $white;
                  }

                  &:after {
                     border-color: #aeaeae;
                  }
               }
            }
         }
      }

      .field-group {
         @include margin-top(80);
      }
   }
}

.signup-form-section {
   .step-form-container {
      .step-form-heading {
         @include padding(30 0 25);

         h3.title {
            @include padding-bottom(10);
         }

         h4.note {
            @include fontsize(16);
         }
      }
