}

.signup-form-section,
.edit-profile-section {
   @include padding-bottom(50);

   .step-form-container {
      @include padding-bottom(50);
   }

   .btn-field {
      @include padding-top(60);
   }

   .upload-file-box {
      text-align: center;

      @include margin-bottom(80);

      .user-pic {
         width: 130px;
         height: 130px;
         overflow: hidden;

         @include circle;

         @include border(1, solid, #a2a7ac);

         margin: 0 auto 10px;

         img {
            min-height: 130px;
            object-fit: cover;
            max-width: 100%;
         }
      }

      h4 {
         @include fontsize(16);

         font-weight: 400;
         text-align: center;
         color: $color-text-05;
      }

      .fileUpload {
         display: $ib;

         @include margin-top(30);
      }
   }

   .t-exp-field {
      display: table;
      width: 100%;

      .c-field {
         .c-field-control {
            max-width: 85px;
            display: $ib;
            vertical-align: middle;
            text-align: center;

            @include margin-right(12);
         }

         span {
            display: $ib;
            vertical-align: middle;

            @include fontsize(16);

            font-weight: 400;
            color: $color-text-05;
         }
      }
   }

   .t-exp-time-field {
      .label-name {
         display: $b;
         text-align: center;
         font-weight: 400;
      }
   }

   .exp-field-group {
      @include padding-top(30);
   }

   .field-opt {
      display: table;
      width: 100%;

      @include margin-bottom(40);

      .add-icon {
         width: 50px;
         height: 50px;
         float: left;

         @include pos(rel, null);

         z-index: 0;
         display: $b;
         text-align: center;

         @include fontsize(22);

         line-height: 52px;
         cursor: pointer;
         color: $white;

         &:before {
            @include pos(ab, 0 0 0 0);

            background-color: $theme;

            @include circle;

            content: "";
            z-index: -1;
         }

         &:after {
            @include pos(ab, 0 0 0 0);

            background-color: lighten($theme, 10%);

            @include circle;

            @include transform(scale(0));

            content: "";
            z-index: -1;
            -webkit-transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
            transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
         }

         &:hover {
            &:after {
               @include transform(scale(1));
            }
         }
      }

      .delete-icon {
         width: 50px;
         height: 50px;
         float: right;

         @include pos(rel, null);

         z-index: 0;
         display: $b;
         text-align: center;

         @include fontsize(22);

         line-height: 50px;
         cursor: pointer;
         color: $color-text-05;

         &:before {
            @include pos(ab, 0 0 0 0);

            background-color: #eaeaea;

            @include circle;

            content: "";
            z-index: -1;
         }

         &:after {
            @include pos(ab, 0 0 0 0);

            background-color: darken(#eaeaea, 10%);

            @include circle;

            @include transform(scale(0));

            content: "";
            z-index: -1;
            -webkit-transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
            transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
         }

         &:hover {
            &:after {
               @include transform(scale(1));
            }
         }
      }
   }
}

.create-liferoute-section {
   .step-form-block {
      .step-form-container {
         .step-form-heading {
            background-color: $color-list-01;

            .sub-title {
               @include fontsize(20);

               font-weight: 300;
            }
         }

         .step-form-nav {
            ul {
               li {
                  .active {
                     background-color: $color-list-01;
                     border-color: $color-list-01;
                  }
               }
            }
         }
      }
   }

   .create-liferoute__step-2 {
      .step-form-block {
         .step-form-container {
            .step-form-field-box {
               max-width: 800px;
            }
         }
      }
   }

   .c-lroute-stepbox {
      @include border(1, solid, lighten(#5b5b5b, 50%));

      @include padding(15);

      @include margin-bottom(25);

      @include box-shadow(0 4px rgba(34, 34, 34, 0.24));

      @include border-round(3);

      background-color: #d2f0d8;

      .c-lroute__top {
         display: table;
         width: 100%;

         .c-title {
            float: left;
            font-weight: 400;

            @include fontsize(18);

            text-transform: uppercase;
            color: $color-text-02;
            letter-spacing: 2px;
         }

         .c-lroute__opt {
            float: right;

            ul {
               li {
                  display: $ib;

                  @include padding-right(15);

                  color: #525252;

                  &:last-child {
                     @include padding-right(0);
                  }

                  span.caret {
                     border-width: 10px;
                     cursor: pointer;
                  }
               }
            }
         }
      }

      .c-lroute__field-group {
         @include margin-top(50);

         .c-field {
            .c-field-control {
               border-radius: 0;
               background-color: $white;
            }
         }
      }

      &.active {
         @include box-shadow(0 2px 5px 1px rgba(0, 0, 0, 0.35));

         .c-lroute__opt {
            ul {
               li {
                  .caret {
                     @include transform(rotate(-180deg));
                  }
               }
            }
         }
      }

      .c-add-img-video__block {
         border-color: darken(#d2f0d8, 30%);

         .data-list {
            figcaption {
               .file-title {
                  border-color: darken(#d2f0d8, 30%);
                  min-height: 50px;
               }
            }
         }
      }
   }

   .c-post_option {
      @include border(1, solid, lighten(#5b5b5b, 50%));

      @include padding(15);

      @include margin-bottom(50);

      h4 {
         @include fontsize(18);

         color: $theme;
         text-transform: uppercase;
         font-weight: 400;
      }

      .c-post_option__row {
         @include padding(20 0);

         @include border-bottom(1, solid, lighten(#5b5b5b, 50%));

         .c-post_option__txt {
            @include fontsize(16);

            h5 {
               @include fontsize(20);

               color: $color-text-02;

               @include padding-bottom(10);

               font-weight: 500;
            }

            color: $color-text-02;

            p {
               font-weight: 300;
            }
         }

         .c-post_option_btn {
            float: right;
         }

         &:last-of-type {
            border-bottom: 0;
         }
      }
   }

   .c-setting-opt {
      @include padding(20 25 20 0);

      @include border-bottom(1, solid, lighten(#5b5b5b, 50%));

      h3 {
         color: $color-text-02;
         font-weight: 300;

         @include fontsize(20);
      }

      .c-oth-opt__btn {
         float: right;
      }
   }

   .c-search-field {
      @include margin-top(30);

      label {
         @include fontsize(14);

         color: $theme;
         font-weight: 500;

         @include padding-top(15);
      }

      .c-field {
         .c-field-control {
            border-radius: 0;
         }
      }

      .people-list {
         @include margin-top(15);

         ul {
            li {
               display: $ib;

               @include fontsize(11);

               font-weight: 500;
               color: $color-text-02;
               background-color: #e7e7e7;

               @include padding(5 10);

               @include border-round(4);

               @include margin-right(10);

               @include margin-bottom(5);
            }
         }
      }
   }

   .btn-field {
      @include margin-top(80);
   }
}

