.c-add-img-video__block {
   @include padding(40 0);

   @include border-bottom(1, solid, #f1f1f1);

   .c-add-img-video__top {
      display: table;
      width: 100%;

      .fileUpload {
         display: $ib;
         vertical-align: middle;
      }

      .help-txt {
         display: $ib;
         vertical-align: middle;
         color: $theme;
         font-weight: 300;

         @include fontsize(18);

         @include margin-left(10);

         &.c-1 {
            color: $color-text-10;
         }

         &.c-2 {
            color: $color-text-09;
         }

         &.c-3 {
            color: $color-text-11;
         }
      }
   }

   .data-list {
      @include margin-top(30);

      .row {
         @include margin-left(-20);

         @include margin-right(-20);

         [class^="col-"] {
            @include padding-left(20);

            @include padding-right(20);
         }
      }

      .img-box {
         @include margin-bottom(10);

         .delete-ico {
            text-align: right;
            color: $theme;
            display: $b;

            @include padding-bottom(10);

            [class^="icon-"] {
               color: $theme;
               cursor: pointer;
            }
         }

         figure {
            @include margin(0);

            overflow: hidden;

            @include fluid-ratio(16 9, img);

            img {
               width: 100%;
            }
         }
      }

      .video-box {
         @include margin-bottom(10);

         .video-js {
            width: 100%;
         }

         .delete-ico {
            text-align: right;
            color: $theme;
            display: $b;

            @include padding-bottom(10);

            [class^="icon-"] {
               color: $theme;
               cursor: pointer;
            }
         }

         figure {
            @include margin(0);

            overflow: hidden;

            @include fluid-ratio(16 9, img);

            img {
               width: 100%;
            }
         }
      }

      figcaption {
         .file-title {
            @include fontsize(12);

            color: $theme;
            font-weight: 500;

            @include padding(15 0);

            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;

            @include border-bottom(1, solid, lighten(#d7d7d7, 10%));
         }

         .caption__opt {
            @include margin-top(20);

            .button {
               @include fontsize(10);

               letter-spacing: 2px;
            }

            .c-edit-field {
               display: table;
               width: 100%;

               input[type="text"] {
                  width: 150px;
                  height: 30px;

                  @include border-round(30);

                  background-color: #ededed;
                  border: 0;
                  outline: none;
                  font-weight: 500;

                  @include fontsize(10);

                  color: #403939;

                  @include padding(0 15);

                  float: left;
               }

               .field-opt {
                  float: right;

                  ul {
                     li {
                        display: $ib;

                        .edit-ico {
                           @include padding-right(10);

                           @include padding-top(5);

                           @include padding-bottom(5);

                           color: $color-text-02;

                           @include border-right(1, solid, #dbdbdb);

                           cursor: pointer;

                           &:hover,
                           &:focus {
                              color: $theme;
                           }
                        }

                        .close-ico {
                           @include padding-left(10);

                           @include padding-top(5);

                           @include padding-bottom(5);

                           color: $color-text-02;
                           cursor: pointer;

                           &:hover,
                           &:focus {
                              color: $theme;
                           }
                        }
                     }
                  }
               }
            }
         }
      }
   }
}