.main-cotainer {
   min-height: 100%;

   @include experimental-value(min-height, calc(100% - 150px));
}

.side-backdrop {
   background-color: rgba(#000, 0.8);

   @include pos(ab, 0 0 0 0);

   z-index: 10;
}

.c-field {
   @include margin-bottom(50);

   .c-field-control {
      width: 100%;
      background-color: transparent;
      font-weight: 100;
      border-radius: 4px;
      font-family: $font-family;

      @include border(1, solid, #b8b8b8);

      @include padding(10 15);

      height: 48px;

      @include fontsize(16);

      font-weight: 400;
      color: $color-text-01;
      outline: none !important;
      text-overflow: ellipsis;

      &::-webkit-input-placeholder {
         /* Chrome/Opera/Safari */
         color: lighten($color-text-01, 10%);
         opacity: 1;
      }

      &::-moz-placeholder {
         /* Firefox 19+ */
         color: lighten($color-text-01, 10%);
         opacity: 1;
      }

      &:-ms-input-placeholder {
         /* IE 10+ */
         color: lighten($color-text-01, 10%);
         opacity: 1;
      }

      &:-moz-placeholder {
         /* Firefox 18- */
         color: lighten($color-text-01, 10%);
         opacity: 1;
      }

      &:disabled {
         background-color: $color-bg-01;

         @include opacity(0.5);

         cursor: not-allowed;

         & + .icon-addon {
            @include opacity(0.5);

            cursor: not-allowed;
            pointer-events: none;
         }
      }
   }

   textarea.c-field-control {
      resize: none;
      height: auto !important;
   }

   &.c-bg-color {
      .c-field-control {
         background-color: $color-bg-01;
      }
   }

   &.c-md {
      .c-field-control {
         @include fontsize(14);
      }

      tags-input {
         .tags {
            @include fontsize(14);

            .input {
               @include fontsize(14);
            }
         }
      }
   }

   &.c-lh {
      .c-field-control {
         height: 55px;
      }

      tags-input {
         .tags {
            min-height: 55px;

            .input {
               height: 30px;
            }
         }
      }
   }

   &.c-sh {
      .c-field-control {
         height: 40px;
      }

      tags-input {
         .tags {
            min-height: 40px;

            .input {
               height: 30px;
            }
         }
      }
   }

   &.c-shadow {
      .c-field-control {
         @include box-shadow(inset 0 3px 14px rgba(0, 0, 0, 0.11));
      }
   }

   &.c-b-lg {
      @include margin-bottom(60);
   }

   &.c-b-sm {
      @include margin-bottom(30);
   }

   &.c-b-xsm {
      @include margin-bottom(15);
   }

   .c-help-note {
      @include fontsize(10);

      color: lighten($color-text-05, 30%);

      @include padding-bottom(15);

      p {
         font-weight: 400;
         line-height: 25px;
         letter-spacing: 1px;
      }
   }

   tags-input {
      .tags {
         @include border(1, solid, #b8b8b8);

         @include padding(10 15);

         min-height: 48px;

         @include fontsize(16);

         font-weight: 400;
         color: $color-text-01;
         outline: none !important;
         border-radius: 4px;

         @include box-shadow(inset 0 3px 14px rgba(0, 0, 0, 0.11));

         .input {
            margin: 0;

            @include fontsize(16);

            outline: none !important;
            width: auto !important;
            color: $color-text-01;
            font-weight: 400;
            font-family: $font-family;
            width: 100% !important;

            &::-webkit-input-placeholder {
               /* Chrome/Opera/Safari */
               color: lighten($color-text-01, 10%);
               opacity: 1;
            }

            &::-moz-placeholder {
               /* Firefox 19+ */
               color: lighten($color-text-01, 10%);
               opacity: 1;
            }

            &:-ms-input-placeholder {
               /* IE 10+ */
               color: lighten($color-text-01, 10%);
               opacity: 1;
            }

            &:-moz-placeholder {
               /* Firefox 18- */
               color: lighten($color-text-01, 10%);
               opacity: 1;
            }
         }

         &.focused {
            -webkit-appearance: none !important;
            -webkit-box-shadow: none;

            @include box-shadow(inset 0 3px 14px rgba(0, 0, 0, 0.11));

            outline: none !important;
         }
      }
   }

   .c-has-icon {
      @include pos(rel, null);

      .c-field-control {
         @include padding-right(37);
      }

      .icon-addon {
         @include pos(ab, 50% 20px null null);

         @include fontsize(20);

         @include translatepos(0, -50%);

         color: $color-text-05;
         cursor: pointer;
      }
   }

   .select-control {
      &:after {
         right: 20px;

         @include arrow(bottom, #444444, 6px);
      }
   }

   label {
      @include fontsize(18);

      font-weight: 400;

      @include padding-bottom(10);

      color: $color-text-05;
   }

   .c-line {
      label {
         @include padding-right(10);

         background-color: $white;
      }

      @include pos(rel, null);

      z-index: 0;

      &:after {
         @include pos(ab, 15px 0 null 0);

         content: "";
         height: 4px;
         z-index: -1;
         background-color: $theme;
      }

      &.c-1 {
         &:after {
            background-color: $color-list-01;
         }
      }

      &.c-2 {
         &:after {
            background-color: $color-list-02;
         }
      }

      &.c-3 {
         &:after {
            background-color: $color-list-03;
         }
      }

      &.c-4 {
         &:after {
            background-color: $color-list-05;
         }
      }

      &.c-5 {
         &:after {
            background-color: $color-list-06;
         }
      }
   }

   &.c-outline {
      .c-field-control {
         &:focus {
            outline: $theme solid 2px !important;
         }
      }

      tags-input {
         .tags {
            &.focused {
               outline: $theme solid 2px !important;
            }
         }
      }

      &.c-1 {
         .c-field-control {
            &:focus {
               outline: $color-list-01 solid 2px !important;
            }
         }

         tags-input {
            .tags {
               &.focused {
                  outline: $color-list-01 solid 2px !important;
               }
            }
         }
      }

      &.c-2 {
         .c-field-control {
            &:focus {
               outline: $color-list-02 solid 2px !important;
            }
         }

         tags-input {
            .tags {
               &.focused {
                  outline: $color-list-02 solid 2px !important;
               }
            }
         }
      }

      &.c-3 {
         .c-field-control {
            &:focus {
               outline: $color-list-03 solid 2px !important;
            }
         }

         tags-input {
            .tags {
               &.focused {
                  outline: $color-list-03 solid 2px !important;
               }
            }
         }
      }

      &.c-4 {
         .c-field-control {
            &:focus {
               outline: $color-list-05 solid 2px !important;
            }
         }

         tags-input {
            .tags {
               &.focused {
                  outline: $color-list-05 solid 2px !important;
               }
            }
         }
      }

      &.c-5 {
         .c-field-control {
            &:focus {
               outline: $color-list-06 solid 2px !important;
            }
         }

         tags-input {
            .tags {
               &.focused {
                  outline: $color-list-06 solid 2px !important;
               }
            }
         }
      }
   }

   .help-txt {
      @include margin-top(30);

      color: $color-list-01;

      span {
         font-weight: 400;
      }
   }
}

.c-field-d-label {
   text-align: center;

   @include fontsize(18);

   text-transform: uppercase;
   color: $color-text-08;
   font-weight: 400;
}

.f-field {
   @include margin-bottom(15);

   .f-field-control {
      width: 100%;
      background-color: transparent;
      font-weight: 100;
      font-family: $font-family;
      z-index: 2;
      height: 40px;
      font-weight: 500;
      color: lighten($color-text-05, 20%);
      outline: none !important;
      text-overflow: ellipsis;

      @include fontsize(13);

      @include pos(r, null);

      @include border(1, solid, #b8b8b8);

      @include padding(10);

      &::-webkit-input-placeholder {
         /* Chrome/Opera/Safari */
         color: lighten($color-text-05, 20%);
         opacity: 1;
      }

      &::-moz-placeholder {
         /* Firefox 19+ */
         color: lighten($color-text-05, 20%);
         opacity: 1;
      }

      &:-ms-input-placeholder {
         /* IE 10+ */
         color: lighten($color-text-05, 20%);
         opacity: 1;
      }

      &:-moz-placeholder {
         /* Firefox 18- */
         color: lighten($color-text-05, 20%);
         opacity: 1;
      }
   }

   .select-control {
      &:after {
         font-family: 'icomoon' !important;
         right: 10px;
         border: 0;
         content: "\e91b";
         color: lighten($color-text-05, 20%);

         @include fontsize(14);

         @include translatepos(0, -50%);

         width: auto;
         height: auto;
         margin-top: 0;
      }
   }

   &.has-icon {
      @include pos(rel, null);

      .f-field-control {
         @include padding-right(25);
      }

      .icon-addon {
         @include pos(ab, 50% 10px null null);

         @include fontsize(16);

         @include translatepos(0, -50%);

         color: $color-text-05;
         cursor: pointer;
      }
   }
}

.page-heading {
   @include fontsize(25);

   text-align: center;
   font-weight: 400;
   color: $color-text-03;

   &.up-case {
      text-transform: uppercase;
   }
}

.t-table {
   display: table;
   width: 100%;
}

.t-cell {
   display: table-cell;
   vertical-align: middle;
}

.app-modal {
   .modal-header {
      background-color: $theme;

      @include padding(25 35);

      color: $white;

      @include border-round(6 6 0 0);

      .title {
         @include fontsize(25);

         font-weight: 400;
      }
   }

   .modal-body {
      @include padding(30 40);
   }

   .close-ico {
      background-color: transparent;

      @include padding(0);

      border: 0;
      color: $white;

      @include fontsize(30);

      cursor: pointer;

      @include pos(ab, 25px 20px null null);
   }

   &.app-modal-sm {
      .modal-header {
         @include padding(15 20);

         .title {
            @include fontsize(20);

            font-weight: 400;
         }
      }

      .close-ico {
         top: 15px;
         right: 20px;

         @include fontsize(18);
      }
   }
}

.blur-modal {
   .top-header,
   .find-path-cotainer,
   .main-cotainer {
      filter: blur(5px);
      -webkit-filter: blur(5px);
   }
}

.cp-modal {
   .modal-header {
      background-color: $color-list-03;

      @include padding(20 40 20 20);

      color: $white;

      @include border-round(6 6 0 0);

      .title {
         @include fontsize(24);

         font-weight: 400;
      }
   }

   .modal-content {
      background-color: transparent;
   }

   .modal-body {
      background-color: $white;
   }

   .close-ico {
      background-color: transparent;

      @include padding(0);

      border: 0;
      color: $white;

      @include fontsize(30);

      cursor: pointer;

      @include pos(ab, 20px 20px null null);
   }
}

.work-history-modal {
   .modal-body {
      @include padding(0 0 70 0);
   }

   .work-history-list {
      max-height: 350px;

      @include pos(r, null);

      //height: auto;

      .work-table {
         width: 100%;
         border-collapse: collapse;
         border-spacing: 0;

         & > thead {
            th {
               @include padding(15);

               font-weight: 400;

               @include fontsize(18);

               background-color: #f0f0f0;
               color: $color-text-02;

               @include border-bottom(1, solid, #d7d7d7);
            }
         }

         tbody {
            td {
               @include padding(15 15);

               font-weight: 300;

               @include fontsize(18);

               color: $color-text-02;

               @include border-right(1, solid, #d7d7d7);

               &:last-child {
                  border-right: 0;
               }

               .ico {
                  color: $theme;
               }
            }

            td[data-column="From"] {
               min-width: 130px;
            }
         }
      }
   }
}

.i-peoplelist-modal {
   .p-list {
      @include padding(10 40 0);

      max-height: 500px;

      .media {
         @include border-bottom(1, solid, #e7e7e7);

         @include padding(20 0);

         @include margin(0);

         .media-left {
            @include padding-right(15);

            .usr-pic {
               img {
                  width: 60px;
                  height: 60px;

                  @include circle;

                  object-fit: cover;
               }
            }
         }

         .usr-name {
            h4 {
               @include fontsize(16);

               font-weight: 300;
               color: $color-text-02;
            }
         }

         .media-left,
         .media-right,
         .media-body {
            vertical-align: middle;
         }

         &:first-child {
            @include padding-top(0);
         }
      }
   }
}

tags-input {
   .tag-list {
      .tag-item {
         background: transparent;
         border: 0;

         @include fontsize(16);

         font-weight: 400;
         font-family: $font-family;

         span {
            display: $ib;
            vertical-align: middle;
         }

         .remove-button {
            width: 16px;
            height: 16px;

            @include border(1, solid, #444444);

            @include circle;

            display: $ib;
            vertical-align: middle;
            text-align: center;
            line-height: 15px;
         }
      }
   }
}

.connection-box {
   background-color: $theme;
   text-align: center;

   @include margin-bottom(28);

   @include border-round(4);

   a {
      @include padding(45 10);

      display: $b;
   }

   h3 {
      color: $white;

      @include fontsize(30);

      font-weight: 500;
   }

   &.c-1 {
      background-color: $color-connetion-01;
   }

   &.c-2 {
      background-color: $color-connetion-02;
   }

   &.c-3 {
      background-color: $color-connetion-04;
   }

   &.c-4 {
      background-color: $color-connetion-03;
   }

   &.c-5 {
      background-color: $color-connetion-05;
   }

   &.c-6 {
      background-color: $color-connetion-06;
   }

   &.c-7 {
      background-color: $color-connetion-07;
   }

   &:hover {
      @include opacity(0.9);
   }
}

.rating-container {
   @include fontsize(22);

   .star-uncheck {
      color: #bab9b9;
   }

   .star-checked {
      color: #ffcd00;
   }
}

.error-mesg {
   @include margin(5 0);

   font-weight: 400;
}

.no-result-msg {
   @include margin(20 0);
}