.m-post-section {
    @include padding(90 0 50);
    @include pos(rel, null);

    .my-post-block {
        @include pos(rel, null);
    }

    .user-dtl-block {
        @include pos(ab, 0 null null 0);
        margin-top: 100px;

        &.push-left {
            left: -1000px;
        }
    }
    .my-post-cotainer {
        -webkit-transition: all 600ms cubic-bezier(0.77, 0, 0.175, 1);
        transition: all 600ms cubic-bezier(0.77, 0, 0.175, 1);
        @include padding-left(390);
        min-height:850px;
        .my-post-list {
            @include margin-top(50);
        }
        .title {
            @include fontsize(30);
            font-weight: 400;
            color: $theme;
            @include padding-bottom(5);
            @include border-bottom(1, solid, $theme);
        }

        &.open {
            @include padding-left(0);
        }
    }
    .sp-btn {
        @include pos(ab, 150px null null -35px);
        z-index: 20;
    }
}


.m-post-liferoute {
    @include padding(100 0 50);

    .filter-ap-msg {
        text-align: center;

        h4 {
            color: $theme;
            font-weight: 300;
            @include fontsize(17);
        }
    }

    .heading-block {
        @include border-bottom(1, solid, $theme);
        @include padding-bottom(10);

        .heading-block__buttons {
            text-align: right;
        }

        .breadcrumb-list {
            @include margin-top(10);
            ul {
                li {
                    display: $ib;

                    a {
                        @include fontsize(30);
                        text-transform: capitalize;
                        font-weight: 400;
                        color: $theme;
                        @include padding(0 15);
                        @include pos(rel, null);
                        &:after {
                            @include pos(ab, -4px -8px 0px null);
                            content: ">";
                        }

                        &.active {
                            color: lighten($theme, 20%);

                            &:after {
                                display: none;
                            }
                        }
                    }


                    &:first-child {
                        a {
                            @include padding-left(0);
                        }
                    }
                }
            }
        }
    }

    .filter-side-block {
        @include margin-top(115);


    }
}


.m-post-liferoute__list {
    @include margin-top(80);
    .total-posted {
        @include padding-bottom(10);
        @include fontsize(14);
        font-weight: 300;
    }
    .l-route-box {
        @include margin-bottom(20);
        background-color: $color-bg-02;

        .user-dtl {
            .user-dtl-info {

                .user-dtl-info-list {

                    .list-info {
                        li {
                            .l-label {
                                width: 110px;
                                @include padding-right(10);
                                &:after {
                                    right: 10px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &.ap-filter {
        @include margin-top(45);
    }
    .filter-ap-msg {
        @include margin-bottom(20);
    }
}



