.filter-side-block {
    .filter-side-box {
        @include padding(20 20 0);

        .filter-block__top {
            display: table;
            width: 100%;
            @include padding-bottom(15);
            @include border-bottom(1, solid, #dedede);
            h4 {
                @include fontsize(16);
                font-weight: 400;
                color: $color-text-02;
                float: left;
            }
            .caret {
                float: right;
                border-width: 6px;
                border-top-color: #000;
                margin-top: 5px;
            }
        }

        .filter-field-group {
            @include margin-top(30);

            @include fontsize(15);

            .filter-inline-field {
                label {
                    font-weight: 400;
                    @include margin-top(8);
                }
            }
        }

        &.active {
            @include box-shadow(-1px 3px 6px rgba(0, 0, 0, .08));
            background-color: $white;
            @include margin-bottom(30);
            @include padding-bottom(20);

            &.margin-bottom {
                @include margin-bottom(30);
            }
            &.margin-top {
                @include margin-top(30);
            }
            .filter-block__top {
                .caret {
                    @include transform(rotate(-180deg));
                }
            }
        }

        .filter-button-group {
            @include margin-top(35);
        }

        .filter-steps {
            @include margin-top(40);
            h3 {
                font-weight: 400;
                @include fontsize(15);
                color: $color-text-02;
            }
            ul {
                @include margin-top(10);
                li {
                    @include padding-bottom(10);
                }
            }
        }
        .search_link {
            color: $color-text-08;
            font-weight: 300;
            @include fontsize(15);
            text-decoration: underline;
            @include margin-top(10);
            display: $ib;
        }
    }
}